import { environment } from './../../../environments/environment.prod';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { User } from '@jgpl/core/auth/user.interface';
import { LoginService } from '@jgpl/core/services/login.service';
// const CryptrValue = require('cryptr');
// const cryptr = new CryptrValue(environment.apiSecreat);
import * as CryptoJS from 'crypto-js';

@Injectable({
  providedIn: 'root'
})

export class AuthService {
  headers = new HttpHeaders().set('Content-Type', 'application/json');
  currentUser = {};
  endpoint: string = environment.baseUrl;
  encryptSecretKey = environment.apiSecreat;
  constructor(
    private http: HttpClient,
    public router: Router,
    public ls: LoginService,
  ) {
  }
  // tslint:disable-next-line: typedef
  encryptData(data: any) {
    let ret: boolean = true;
    try {
      return CryptoJS.AES.encrypt(JSON.stringify(data), this.encryptSecretKey).toString();
    } catch (e) {
      console.log(e);
    }
    return ret;
  }

  // tslint:disable-next-line: typedef
  decryptData(data: any) {

    let ret: boolean = true;
    try {
      const bytes = CryptoJS.AES.decrypt(data, this.encryptSecretKey);
      if (bytes.toString()) {
        return JSON.parse(bytes.toString(CryptoJS.enc.Utf8));
      }
      return data;
    } catch (e) {
      console.log(e);
    }
    return ret;
  }
  // Sign-in
  // tslint:disable-next-line: typedef
  signIn(user: User) {
    const encrypted = this.encryptData(user.password);
    const data = {
      email: user.email,
      password: encrypted
    }
    // console.log(data);
    // console.log(encrypted);
    // tslint:disable-next-line: deprecation
    this.ls.postLoginUser(data).subscribe((res: any) => {
      // console.log(res.data.token);
      if (res){
        localStorage.setItem('access_token', res.data.token);
        this.router.navigate(['dashboard']);
      } else {
        console.log('no data Found');
      }
    });
  }

  // tslint:disable-next-line: typedef
  getToken() {
    return localStorage.getItem('access_token');
  }

  get isLoggedIn(): boolean {
    const authToken = localStorage.getItem('access_token');
    return (authToken !== null) ? true : false;
  }

  // tslint:disable-next-line: typedef
  doLogout() {
    const removeToken = localStorage.removeItem('access_token');
    if (removeToken == null) {
      this.router.navigate(['login']);
    }
  }

  // Error
  // tslint:disable-next-line: typedef
  handleError(error: HttpErrorResponse) {
    let msg = '';
    if (error.error instanceof ErrorEvent) {
      // client-side error
      msg = error.error.message;
    } else {
      // server-side error
      msg = `Error Code: ${error.status}\nMessage: ${error.message}`;
    }
    return throwError(msg);
  }
}
