import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from './helpers/auth.guard';
import { AppCustomPreloader } from './app-routing.loader';
import { MasterlayoutComponent } from './shared/masterlayout/masterlayout.component';
import { APP_BASE_HREF } from '@angular/common';
const routes: Routes = [
  {
    path: 'contacts',
    loadChildren: () => import('./pages/frontend/contacts/contacts.module').then(m => m.ContactsModule),
    pathMatch: 'full'
  },
  {
    path: 'about-us',
    loadChildren: () => import('./pages/frontend/about-us/about-us.module').then(m => m.AboutUsModule),
    pathMatch: 'full'
  },
  {
    path: 'portfolio',
    loadChildren: () => import('./pages/frontend/portfolio/portfolio.module').then(m => m.PortfolioModule),
    pathMatch: 'full'
  },
  // {
  //   path: 'portfolio-details',
  //   loadChildren: () => import('./pages/frontend/portfolio-details/portfolio-details.module').then(m => m.PortfolioDetailsModule),
  //   pathMatch: 'full'
  // },
  {
    path: 'franchise',
    loadChildren: () => import('./pages/frontend/franchises/franchises.module').then(m => m.FranchisesModule),
    pathMatch: 'full'
  },
  {
    path: 'faq',
    loadChildren: () => import('./pages/frontend/faq/faq.module').then(m => m.FaqModule),
    pathMatch: 'full'
  },
  {
    path: 'term',
    loadChildren: () => import('./pages/frontend/term/term.module').then(m => m.TermModule),
    pathMatch: 'full'
  },
  {
    path: 'policies',
    loadChildren: () => import('./pages/frontend/policies/policies.module').then(m => m.PoliciesModule),
    pathMatch: 'full'
  },

  {
    path: 'team',
    loadChildren: () => import('./pages/frontend/team/team.module').then(m => m.TeamModule),
    pathMatch: 'full'
  },
  {
    path: 'home',
    loadChildren: () => import('./pages/frontend/home/home.module').then(m => m.HomeModule),
    pathMatch: 'full'
  },
  {
    path: 'login',
    loadChildren: () => import('./pages/frontend/login/login.module').then(m => m.LoginModule),
    pathMatch: 'full'
  },
  // {
  //   path: 'dashboard2',
  //   loadChildren: () => import('./pages/backend/dashboard/dashboard.module').then(m => m.DashboardModule),
  //   canActivate: [AuthGuard],
  //   pathMatch: 'full'
  // },
  // {
  //   path: 'dashboard3',
  //   loadChildren: () => import('./shared/masterlayout/masterlayout.module').then(m => m.MasterlayoutModule),
  //   canActivate: [AuthGuard],
  //   pathMatch: 'full'
  // },
  {
    path: 'dashboard',
    component: MasterlayoutComponent,
    children: [
      {
        path: 'dashhome',
        loadChildren: () => import('./pages/backend/dashboard/dashboard.module').then(m => m.DashboardModule),
        canActivate: [AuthGuard],
      },
      { path: '', redirectTo: 'dashhome', pathMatch: 'full' },
    ],
    canActivate: [AuthGuard],
  },
  {
    path: 'settings',
    component: MasterlayoutComponent,
    children: [
      {
        path: 'banner',
        loadChildren: () => import('./pages/backend/banner/banner.module').then(m => m.BannerModule),
        canActivate: [AuthGuard],
      },
      { path: '', redirectTo: 'banner', pathMatch: 'full' },
    ],
    canActivate: [AuthGuard],
  },

  {
    path: '404',
    loadChildren: () => import('./pages/pagenotfound/pagenotfound.module').then(m => m.PagenotfoundModule),
    pathMatch: 'full'
  },

  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: '**', redirectTo: '/404' },

];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash:true })],
  exports: [RouterModule],
  providers: [AppCustomPreloader,{
    provide: APP_BASE_HREF, useValue : '!'
  }]

})
export class AppRoutingModule { }
