import { map, startWith } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { Observable } from 'rxjs';
import { Component, OnInit, Renderer2, ViewChild, ElementRef, ViewEncapsulation } from '@angular/core';
import { SearchService } from '@jgpl/core/services/search.service';

export interface State {
  state: string;
  state_id: number;
  id: number;
}
export interface District {
  district: string;
  district_id: number;
  id: number;
}
@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SearchComponent implements OnInit {
  constructor(private renderer: Renderer2,private ss:SearchService) {


        this.ss.getStateList().subscribe((res: any) => {
          // console.log(res.data);
          this.states = res.data;
          setInterval( () => {
            this.filteredStates = this.stateCtrl.valueChanges
            .pipe(
              startWith(''),
              map(state => state ? this._filterStates(state) : this.states.slice())
            );
          }, 150);
        });

        this.ss.getDistrictList().subscribe((res1: any) => {
          // console.log(res.data);
          this.districts = res1.data;
          setInterval( () => {
            this.filteredDistricts = this.districtCtrl.valueChanges
            .pipe(
              startWith(''),
              map(district => district ? this._filterDistricts(district) : this.districts.slice())
            );
          }, 150);
        });


  }
  state: any;
  stateCtrl = new FormControl();
  districtCtrl = new FormControl();
  district:any;
  filteredStates: Observable<State[]>;
  filteredDistricts: Observable<District[]>;


  // tslint:disable-next-line: member-ordering
  states: State[] = [

    {
      state: 'TELANGANA',
      state_id: 1,
      id: 1,
    },
    {
      state: 'ASSAM',
      state_id: 17368,
      id: 2,
    },
    {
      state: 'BIHAR',
      state_id: 20171,
      id: 3,
    },
    {
      state: 'CHATTISGARH',
      state_id: 29234,
      id: 4,
    },
    {
      state: 'DELHI',
      state_id: 32384,
      id: 5,
    },
  ];
  districts: District[] = [
    {
      district: 'Adilabad',
      district_id: 1,
      id: 1
    },
    {
      district: 'Warangal',
      district_id: 519,
      id: 2
    },
    {
      district: 'Karim Nagar',
      district_id: 939,
      id: 3
    },
    {
      district: 'Mahabub Nagar',
      district_id: 1379,
      id: 4
    },
    {
      district: 'K.V.Rangareddy',
      district_id: 1456,
      id: 5
    },

  ];
  distvalues: any;

  private _filterStates(value: string): State[] {
    const filterValue = value.toLowerCase();
    return this.states.filter(state => state.state.toLowerCase().indexOf(filterValue) === 0);
  }
  private _filterDistricts(value: string): District[] {
    const filterValue = value.toLowerCase();
    return this.districts.filter(district => district.district.toLowerCase().indexOf(filterValue) === 0);
  }

  // tslint:disable-next-line: typedef
  selectedState(state: any, state_id: any) {
    // console.log(state);
    // console.log(state_id);
    this.ss.getDistrictList(state_id).subscribe((res1: any) => {
      // console.log(res.data);
      this.districts = res1.data;
      setInterval( () => {
        this.filteredDistricts = this.districtCtrl.valueChanges
        .pipe(
          startWith(''),
          map(district => district ? this._filterDistricts(district) : this.districts.slice())
        );
      }, 150);
    });
  }
  selectedDistrict(district: any, id: any) {
    console.log(district);
    console.log(id);
  }

  ngOnInit() {


  }

}
