import { environment } from './../../../environments/environment.prod';
import URLs from '@jgpl/core/constants/urls';
import { Injectable } from '@angular/core';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { HttpClient, HttpHeaders, HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { isPlatformBrowser } from '@angular/common';
import { ClassProvider, FactoryProvider, InjectionToken, PLATFORM_ID } from '@angular/core';
import { User } from '@jgpl/core/auth/user.interface';
import { AuthService } from '@jgpl/core/auth/auth.service';
AuthService
@Injectable({
  providedIn: 'root'
})
export class SearchService {
  constructor(
    private router: Router,
    private http: HttpClient,
    private auth: AuthService,
  ) {
  }

  /* GET Methods(){
    list all the Get Methods Under Here.
  }*/
  public getStateList() {
    var access: any = this.auth.getToken;
    return this.http.get<any>(`${URLs.getStateList}`, {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        'ApiSecret': environment.apiSecreat,
        'Authorization': access,
      })
    });
  }

  public getDistrictList(state_id=null){
    var access: any = this.auth.getToken;
    if(state_id==null){
      return this.http.get<any>(`${URLs.getDistrictList}`, {
        headers: new HttpHeaders({
          'Content-Type': 'application/json',
          'ApiSecret': environment.apiSecreat,
          'Authorization': access,
        })
      });
    } else {
      return this.http.get<any>(`${URLs.getDistrictList + '/' + state_id}`, {
        headers: new HttpHeaders({
          'Content-Type': 'application/json',
          'ApiSecret': environment.apiSecreat,
          'Authorization': access,
        })
      });
    }

  }




  /* POST Methods(){
    list all the Post Methods Under Here.
  }*/

  // User Login using this method takes the email and password and gives out the Bearer key
  // tslint:disable-next-line: typedef


}
